import { ref } from '@vue/composition-api'

export default {
  name: 'SmartModal',
  props: {
    title: {
      type: String,
      default: '智慧工地通知'
    },
    content: {
      type: String,
      default: '默认内容'
    },
    maskColor: {
      type: String,
      default: 'rgba(0, 0, 0, 0.5)'
    },
    contentBgColor: {
      type: String,
      default: '#ffffff'
    },
    width: {
      type: [String, Number],
      default: 520
    },
    height: {
      type: [String, Number],
      default: 'auto'
    },
    maxHeight: {
      type: [String, Number],
      default: '80vh'
    },
    contentHeight: {
      type: [String, Number],
      default: 'auto'
    },
    contentMaxHeight: {
      type: [String, Number],
      default: 'calc(80vh - 120px)'
    },
    showIcon: {
      type: Boolean,
      default: true
    },
    iconType: {
      type: String,
      default: 'notification'
    },
    iconColor: {
      type: String,
      default: '#1890ff'
    },
    footerText: {
      type: String,
      default: ''
    }
  },
  setup() {
    const visible = ref(false)

    const showModal = () => {
      visible.value = true
    }

    const handleOk = () => {
      visible.value = false
    }

    const handleCancel = () => {
      visible.value = false
    }

    return {
      visible,
      showModal,
      handleOk,
      handleCancel
    }
  }
}
